import React  from 'react'
import Pricing from './Pricing';
import {useSelector } from 'react-redux'
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import './Home.css';
import Contactus from './contactus';
import favicon from "./../image/favicon.png"

const Home = () => { 
  const userdata = useSelector(state => state.Login.userData);


      if(Object.keys(userdata).length >= 1){
        if(userdata.login){
          window.username = userdata.consumer.name;
          // window.userlogin = userdata.login
       }}

  
  
  return (
    <div>
    <CssBaseline/>
      <div  className='heroContent'>
          
          <Container maxWidth="sm" className="maincontent">
              <span className="firstline">
              {userdata.login ?
                <span className="name">HELLO {window.username.toUpperCase()} </span>
                :
                <span className="offline"> WELCOME TO THE BLOOMAA</span>
              }
              </span>
           
            
          </Container>
          <div style={{ backgroundImage: `url(${favicon})` }} className="heroimg"></div>
        </div>
      <Pricing/>
      <Contactus/>
      
    </div>
  )
}

export default Home


