import React  from 'react';
import Login from './components/Signin/Login';
import Register from './components/Signin/Register';
import Home from './Home/Home'
import './App.css';
import Forgot from './components/Signin/Forgot';
import Navigation from './Nav/Nav';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { Provider } from 'react-redux';
import Otpvery from './Component/Otpvery';
import Otpverypass from './Component/Otpverypass'
import MyProfile from './components/My_Profile/My_Profile';
import AddressUpdate from './components/My_Profile/Address_Update';
import {store , persistor} from './Redux/Store';
import {PersistGate} from 'redux-persist/integration/react'
import MyPlan from './components/My_Plan/My_Plan';
import ChangePass from './components/Signin/ChangePass';
import NumberUpdate from './components/My_Profile/á¹†umber_Update';
import OtpNumber from './Component/OtpNumber';
import Checkout from './Home/checkout';

function App() {

  
  return (
    <Provider store={store}>
    <PersistGate persistor={persistor}>
    <div className="App">
      
      <Router>
        <Navigation/>
        <Switch>
          <Route exact path='/Blooma/'>
            <Home/>
          </Route>
          <Route exact path='/'>
            <Home/>
          </Route>
          <Route path='/Blooma/Login'>
            <Login/>
          </Route>
          <Route path='/Blooma/Register'>
            <Register/>
          </Route>
          <Route path='/Blooma/Forgot'>
            <Forgot/>
          </Route>
          <Router path='/Blooma/OTP'>
            <Otpvery/>
          </Router>
         <Router path='/Blooma/otppass'>
            <Otpverypass/>
          </Router>
          <Route path='/Blooma/my_profile'>
            <MyProfile/>
          </Route>
          <Route path='/Blooma/Address_Update'>
            <AddressUpdate/>
          </Route>
          <Route path='/Blooma/My_Plan'>
            <MyPlan/>
          </Route>
          <Route exact path='/Blooma/ChangePass'>
            <ChangePass/>
          </Route>
          <Route path='/Blooma/Update_Number'>
            <NumberUpdate/>
          </Route>
          <Route path ='/Blooma/otpnumber'>
            <OtpNumber/>
          </Route>
          <Route path ='/Blooma/checkout'>
            <Checkout/>
          </Route>
        </Switch>
      </Router>
      
    </div>
    </PersistGate>
    </Provider>
  );
}

export default App;
